import React from 'react';
import { Glyphicon } from 'react-bootstrap';

const Favs = () => (
  <div>
    <h3><Glyphicon glyph="star" style={{color: 'gold'}} /> favs</h3>
    <p>
      {
        ''
      }
    </p>
    <h4></h4>
    <ul>
      <li><strong>:</strong> <Glyphicon glyph="star" /></li>
      <li><strong>:</strong> <Glyphicon glyph="star" /> favs</li>
    </ul>
    <h4></h4>
    <ul>
      <li></li>
      <li>(: ChromeSafari)(PC)</li>
    </ul>
  </div>
);

export default Favs;



